
.page-header {
  padding: 4px;
  background-color: aliceblue;
  border-radius: 4px;
}

.page-header_nav {
  width: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header_btn {
  display: grid;
}

.page-header_btn svg {
  width: 22px;
  height: 37px;
  fill: none;
  stroke: #1A1A1A;
  stroke-width: 4px;
}

.page-header_btn--forward {
  transform: rotate(180deg);
}
